export async function getFilms() {
  try {
    let response = await fetch(`https://api.nomoreparties.co/beatfilm-movies`, {
      method: 'GET',
    });
    if (response.ok) {
      return response.json();
    };
    throw new Error('    . ,      .      ');
  } catch (err) {
    console.error(err);
    throw err.message;
  }
};
